import Vue from 'vue'

const components = {
  Form: () => import('../../components/Form.vue' /* webpackChunkName: "components/form" */).then(c => c.default || c),
  HackerNewsFinder: () => import('../../components/HackerNewsFinder.vue' /* webpackChunkName: "components/hacker-news-finder" */).then(c => c.default || c),
  Header: () => import('../../components/Header.vue' /* webpackChunkName: "components/header" */).then(c => c.default || c),
  List: () => import('../../components/List.vue' /* webpackChunkName: "components/list" */).then(c => c.default || c),
  Logo: () => import('../../components/Logo.vue' /* webpackChunkName: "components/logo" */).then(c => c.default || c),
  UtilsFetchConfig: () => import('../../components/utils/fetch.config.js' /* webpackChunkName: "components/utils-fetch-config" */).then(c => c.default || c)
}

for (const name in components) {
  Vue.component(name, components[name])
  Vue.component('Lazy' + name, components[name])
}
